import React, { useState, useEffect } from "react";
import axios from "axios";

const Giphy = ({ data, onClick }) => {
  const { text, active, id, voteCount } = data;
  const url = "https://api.giphy.com/v1/gifs/search?api_key=";
  const key = "lUxuo0G2Dg5qHDcas6AXWCY2RY8Y0td7";
  const q = `&q=${encodeURI(text)}&limit=1&offset=0&rating=G&lang=en`;

  const [gif, setGif] = useState({});
  useEffect(() => {
    axios
      .get(url + key + q)
      .then(response => {
        setGif(response.data.data[0]);
      })
      .catch(error => {
        // handle error
        console.log(error);
      });
  }, [text]);

  if (gif && gif.images) {
    return (
      <div onClick={() => onClick(id)} className="gif">
        <img src={gif.images.fixed_width_downsampled.url} />{" "}
        <div className="votes">{voteCount}</div>
        {active ? null : <div className="inactive"></div>}
      </div>
    );
  }
  return <div></div>;
};

export default Giphy;
